.equipment {
  min-height: 774px;
  position: relative;
  padding: 70px 0 92px 0;
}
@media only screen and (max-width: 769px) {
  .equipment {
    padding: 40px 0;
  }
}
.equipment__logo {
  z-index: 0;
  position: absolute;
  top: -121px;
  left: 0;
  pointer-events: none;
}
.equipment__caption {
  position: relative;
  z-index: 1;
  font-size: 40px;
  line-height: 56px;
  font-weight: 700;
  color: #214250;
  margin-bottom: 30px;
}
@media only screen and (max-width: 769px) {
  .equipment__caption {
    font-size: 30px;
    line-height: 46px;
  }
}
.equipment__list {
  display: grid;
  gap: 36px;
  list-style: none;
  padding: 0;
  margin: 0;
  grid-template-columns: repeat(4, 1fr);
}
@media only screen and (max-width: 1550px) {
  .equipment__list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media only screen and (max-width: 1212px) {
  .equipment__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (max-width: 769px) {
  .equipment__list {
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }
}
.equipment__item {
  position: relative;
  background-color: #F7F8F5;
  padding: 10px;
  border-radius: 20px;
  display: flex;
  justify-content: stretch;
  align-items: stretch;
  min-width: 333px;
  min-height: 238px;
}
@media only screen and (max-width: 769px) {
  .equipment__item {
    min-width: 100%;
    min-height: 200px;
  }
}
.equipment__link {
  position: absolute;
  inset: 0;
  padding: 10px;
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
@media (hover: hover) {
  .equipment__link:hover .equipment__arrow {
    background-color: #4f7c12;
  }
  .equipment__link:hover .equipment__name {
    opacity: 0.8;
  }
  .equipment__link:hover .equipment__image {
    height: 170px;
  }
}
.equipment__image {
  display: flex;
  justify-content: center;
  height: 160px;
  transition: height 0.3s;
}
.equipment__image img {
  object-fit: contain;
}
@media only screen and (max-width: 769px) {
  .equipment__image {
    height: 130px;
  }
}
.equipment__name {
  font-size: 18px;
  line-height: 25px;
  font-weight: 600;
  color: #214250;
  text-align: center;
  text-transform: uppercase;
  transition: opacity 0.3s;
}
@media only screen and (max-width: 769px) {
  .equipment__name {
    font-size: 14px;
    line-height: 20px;
  }
}
.equipment__arrow {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background-color: #79BF1B;
  transition: background-color 0.3s;
  background-image: url("../img/arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center center;
}