.news {
  position: relative;
  padding: 46px 0 166px 0;
}
.news__caption {
  font-size: 40px;
  line-height: 56px;
  font-weight: 700;
  color: #214250;
  margin-bottom: 0;
}
@media only screen and (max-width: 769px) {
  .news__caption {
    font-size: 30px;
    line-height: 46px;
  }
}
.news__logo {
  z-index: -1;
  position: absolute;
  top: 90px;
  right: 0;
  pointer-events: none;
}
.news__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}
@media only screen and (max-width: 769px) {
  .news__header {
    margin-bottom: 30px;
  }
}
.news__items {
  display: flex;
  gap: 36px;
}
@media only screen and (max-width: 769px) {
  .news__items {
    flex-wrap: wrap;
  }
}
.news__item {
  width: 33.333%;
}
@media only screen and (max-width: 769px) {
  .news__item {
    width: 100%;
  }
}
@media (hover: hover) {
  .news__item:hover .news__img img {
    width: 110%;
    height: 110%;
  }
  .news__item:hover .news__item-caption {
    opacity: 0.8;
  }
}
.news__date {
  font-size: 14px;
  line-height: 20px;
  color: #214250;
}
.news__img {
  position: relative;
  height: 286px;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 18px;
}
.news__img img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: width 0.3s, height 0.3s;
}
@media only screen and (max-width: 1272px) {
  .news__img {
    height: 220px;
  }
}
.news__item-caption {
  color: #214250;
  font-weight: 600;
  font-size: 24px;
  line-height: 33px;
  margin-top: 10px;
  transition: color 0.3s, opacity 0.3s;
}
@media only screen and (max-width: 1272px) {
  .news__item-caption {
    font-size: 18px;
    line-height: 28px;
  }
}
.news__leefs {
  position: absolute;
  width: 420px;
  height: 141px;
}
.news__leefs--r {
  left: 0;
  bottom: -30px;
}
.news__leefs--l {
  bottom: 0px;
  right: 0;
}
@media only screen and (max-width: 769px) {
  .news__leefs--r {
    display: none;
  }
}