.header {
  background-color: transparent;
  position: relative;
  z-index: 99;
  height: 184px;
  width: 100%;
  background-image: url("../img/header-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  transition: box-shadow 0.3s;
}
@media only screen and (max-width: 769px) {
  .header {
    background-color: rgb(201, 239, 248);
    height: auto;
    background-size: 200% 122%;
    background-position: center bottom -20px;
  }
}
.header__wrapper {
  position: relative;
  padding-top: 35px;
  width: 100%;
  display: flex;
}
@media only screen and (max-width: 1199px) {
  .header__wrapper {
    justify-content: space-between;
  }
}
@media only screen and (max-width: 769px) {
  .header__wrapper {
    padding: 20px 0;
    align-items: center;
  }
}
.header__logo {
  display: flex;
  flex-shrink: 0;
  gap: 25px;
  margin-right: auto;
  margin-left: 40px;
}
.header__logo img {
  position: relative;
  top: -5px;
  flex-shrink: 0;
  width: 248px;
  height: 45px;
}
.header__logo span {
  flex-shrink: 0;
  font-size: 13px;
  line-height: 21px;
  color: #8E8F8F;
}
@media only screen and (max-width: 1302px) {
  .header__logo {
    margin-left: 20px;
  }
  .header__logo span {
    display: none;
  }
}
@media only screen and (max-width: 769px) {
  .header__logo {
    margin: 0 auto;
  }
  .header__logo img {
    width: 165px;
    height: 30px;
    top: auto;
  }
}
.header__contacts {
  display: flex;
  width: 50%;
  margin-left: 40px;
  justify-content: space-between;
}
@media only screen and (max-width: 1199px) {
  .header__contacts {
    margin-left: 0;
  }
}
@media only screen and (max-width: 769px) {
  .header__contacts {
    display: none;
  }
}
.header__mail {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  font-size: 14px;
  line-height: 22px;
  margin-right: 40px;
}
.header__mail a {
  font-weight: 600;
  color: #214250;
  transition: opacity 0.3s;
}
.header__mail a:hover {
  opacity: 0.7;
}
.header__mail span {
  color: #8E8F8F;
}
@media only screen and (max-width: 1199px) {
  .header__mail {
    margin-right: 20px;
  }
}
.header__tel {
  display: flex;
  align-items: center;
  gap: 30px;
}
.header__tel span {
  font-size: 14px;
  line-height: 22px;
  color: #8E8F8F;
}
.header__tel a {
  font-size: 18px;
  line-height: 25px;
  color: #214250;
  font-weight: 600;
  transition: opacity 0.3s;
}
.header__tel a:hover {
  opacity: 0.7;
}
.header__tel-list {
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 1199px) {
  .header__tel span {
    display: none;
  }
}
.header__mobile-contacts {
  display: none;
  margin-top: 30px;
  padding-bottom: 30px;
}
.header__mobile-contacts .header__mail {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 20px;
  margin-right: 0;
}
.header__mobile-contacts .header__mail a {
  font-weight: 600;
  color: #214250;
  text-align: center;
}
.header__mobile-contacts .header__mail span {
  color: #ffffff;
}
@media only screen and (max-width: 769px) {
  .header__mobile-contacts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.header__nav {
  width: 100%;
  background-color: #66A316;
  padding: 14px 0;
  margin-top: 50px;
  position: relative;
  transition: opacity 0.3s, transform 0.3s;
}
.header__nav-list {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}
.header__nav-item a {
  color: #ffffff;
  transition: opacity 0.3s;
}
.header__nav-item a:hover {
  opacity: 0.7;
}
@media only screen and (max-width: 769px) {
  .header__nav {
    pointer-events: none;
    opacity: 0;
    transform: translateX(-100%);
    position: absolute;
    z-index: 10;
    top: 36px;
    left: 0;
  }
  .header__nav-list {
    flex-direction: column;
    gap: 10px;
    text-align: center;
  }
}
.header__nav-more {
  pointer-events: none;
  display: flex;
  position: absolute;
  z-index: 10;
  top: -30px;
  left: 0;
  width: 250px;
  padding: 20px;
  border: 1px solid #ffffff;
  box-shadow: 0px 0px 15px 0px #518311;
  border-radius: 10px;
  border-top-left-radius: 0;
  flex-direction: column;
  gap: 10px;
  list-style: none;
  margin: 0;
  background-color: #578b13;
  transition: opacity 0.3s;
  opacity: 0;
}
@media only screen and (max-width: 769px) {
  .header__nav-more {
    position: relative;
    width: 100%;
    top: auto;
    left: auto;
    background-color: transparent;
    padding: 0;
    border: none;
    box-shadow: none;
    opacity: 1;
    pointer-events: auto;
    text-align: center;
  }
}
.header__nav-more-item {
  margin: 0;
}
.header__nav-more-item a {
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  text-transform: uppercase;
  transition: opacity 0.3s;
}
@media (hover: hover) {
  .header__nav-more-item a:hover {
    opacity: 0.7;
  }
}
.header__burger {
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 46px;
  height: 46px;
  border-radius: 6px;
  background-color: #66A316;
  flex-shrink: 0;
  padding: 8px;
  position: relative;
  z-index: 3;
  cursor: pointer;
  transition: background-color 0.3s;
}
.header__burger i {
  display: block;
  height: 3px;
  width: 100%;
  border-radius: 2px;
  background: #fff;
  left: 0;
  top: 0;
  margin-bottom: 6px;
}
.header__burger i:first-child {
  width: 50%;
}
.header__burger i:last-child {
  margin-bottom: 0;
}
@media (hover: hover) {
  .header__burger:hover {
    background-color: #588d13;
  }
}

@media only screen and (max-width: 769px) {
  .scrollable-nav-container {
    height: calc(100vh - 100px);
    overflow-y: scroll;
  }
}

body.is-nav-open .header__nav-more {
  opacity: 1;
  pointer-events: auto;
}
@media only screen and (max-width: 769px) {
  body.is-nav-open .header__nav {
    opacity: 1;
    pointer-events: auto;
    transform: translateX(0);
  }
  body.is-nav-open .header__nav-list {
    flex-direction: column;
    gap: 10px;
  }
}
body.is-nav-open .header__burger {
  box-shadow: inset 0px 0px 15px 0px #518311;
}
@media (hover: hover) {
  body.is-nav-open .header__burger:hover {
    background-color: #66a316;
  }
}
@media only screen and (max-width: 769px) {
  body.is-nav-open {
    overflow: hidden;
  }
}

body.is-scrolled .header {
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0px 11px 15px -10px #000000;
}
body.is-scrolled .main {
  padding-top: 185px;
}
@media only screen and (max-width: 769px) {
  body.is-scrolled .main {
    padding-top: 86px;
  }
}