.button {
  display: inline-flex;
  align-items: center;
  position: relative;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 600;
  padding: 18px 40px;
  text-align: center;
  justify-content: center;
  color: #FFFFFF;
  border: none;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s, opacity 0.3s, box-shadow 0.3s;
}
.button--sm {
  background-color: #E0F7FD;
  color: #214250;
  padding: 10px;
  border-radius: 8px;
  height: 40px;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
}
.button--sm:hover {
  background-color: #b0ebfa;
}
.button--green {
  background-color: #79BF1B;
  background: linear-gradient(0deg, #518311 0%, #79BF1B 100%);
}
.button--green:hover {
  box-shadow: inset 0px 0px 15px 0px #518311;
}