.hero {
  width: 100%;
  min-height: 683px;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.hero--atom {
  background-image: none;
  background-color: #F2FCFF;
}
.hero__content {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}
.hero__content--atom {
  padding-top: 60px;
}
@media only screen and (max-width: 769px) {
  .hero__content--atom {
    padding-top: 0;
  }
}
.hero__caption {
  font-weight: 700;
  font-size: 48px;
  line-height: 67px;
  color: #214250;
  margin-bottom: 24px;
}
.hero__caption--atom {
  font-weight: 600;
  font-size: 36px;
  line-height: 58px;
}
@media only screen and (max-width: 1842px) {
  .hero__caption--atom {
    font-weight: 600;
    font-size: 30px;
    line-height: 50px;
  }
}
@media only screen and (max-width: 1272px) {
  .hero__caption {
    text-align: center;
  }
}
@media only screen and (max-width: 769px) {
  .hero__caption {
    font-weight: 600;
    font-size: 28px;
    line-height: 38px;
  }
}
.hero__wrapper {
  display: flex;
  padding: 61px 0;
  gap: 36px;
}
.hero__wrapper--atom {
  padding: 55px 0;
  gap: 96px;
}
@media only screen and (max-width: 1272px) {
  .hero__wrapper {
    padding: 30px 0;
    flex-direction: column-reverse;
    align-items: center;
  }
}
@media only screen and (max-width: 769px) {
  .hero__wrapper--atom {
    padding: 55px 0;
    gap: 40px;
  }
}
.hero__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 52px;
}
@media only screen and (max-width: 1272px) {
  .hero__list {
    text-align: center;
  }
}
@media only screen and (max-width: 769px) {
  .hero__list {
    gap: 16px;
  }
}
.hero__button {
  width: 244px;
}
@media only screen and (max-width: 1272px) {
  .hero__button {
    margin: 0 auto;
  }
}
.hero__item {
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
  color: #214250;
}
@media only screen and (max-width: 769px) {
  .hero__item {
    font-size: 16px;
    line-height: 24px;
  }
}
.hero__img {
  position: relative;
  z-index: 1;
  width: 629px;
  height: 561px;
  flex-shrink: 0;
}
.hero__img--atom {
  width: 702px;
  height: 600px;
}
@media only screen and (max-width: 1842px) {
  .hero__img--atom {
    width: 629px;
    height: 561px;
  }
}
@media only screen and (max-width: 769px) {
  .hero__img {
    width: 100%;
    height: auto;
  }
  .hero__img img {
    width: 100%;
  }
}
.hero__atom-logo {
  position: absolute;
  top: 360px;
  left: -175px;
  width: 254px;
  height: 185px;
}
@media only screen and (max-width: 1030px) {
  .hero__atom-logo {
    top: 0;
    left: 0;
  }
}
@media only screen and (max-width: 769px) {
  .hero__atom-logo {
    display: none;
  }
}
.hero__atom-leefs {
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 420px;
  height: 141px;
}
@media only screen and (max-width: 1272px) {
  .hero__atom-leefs {
    bottom: 250px;
  }
}
@media only screen and (max-width: 769px) {
  .hero__atom-leefs {
    bottom: -100px;
    right: -200px;
  }
}