@charset "UTF-8";
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #214250;
  color: #ffffff;
  width: 100%;
}
.footer__caption {
  font-size: 16px;
  line-height: 24px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 13px;
}
@media only screen and (max-width: 769px) {
  .footer__caption {
    font-size: 14px;
    line-height: 20px;
  }
}
.footer__address {
  font-style: normal;
  font-size: 14px;
  line-height: 22px;
}
.footer__nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.footer__nav-link {
  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
  color: #ffffff;
  transition: opacity 0.3s;
}
.footer__nav-link:hover {
  opacity: 0.7;
}
.footer__nav-item {
  text-transform: uppercase;
}
@media only screen and (max-width: 769px) {
  .footer__nav {
    gap: 10px;
  }
  .footer__nav-link {
    font-size: 14px;
    line-height: 20px;
  }
}
.footer__equipment {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.footer__equipment-link {
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #ffffff;
  transition: opacity 0.3s;
}
.footer__equipment-link:hover {
  opacity: 0.7;
}
@media only screen and (max-width: 769px) {
  .footer__equipment-link {
    font-size: 12px;
    line-height: 16px;
  }
}
.footer__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.footer__columns {
  display: flex;
  justify-content: space-between;
}
@media only screen and (max-width: 1100px) {
  .footer__columns {
    flex-wrap: wrap;
  }
}
@media only screen and (max-width: 1100px) {
  .footer__column {
    width: 44%;
  }
  .footer__column--сontacts {
    margin-top: 40px;
    width: 100%;
  }
}
.footer__mail {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 22px;
}
.footer__mail a {
  font-weight: 600;
  color: #ffffff;
  transition: opacity 0.3s;
}
.footer__mail a:hover {
  opacity: 0.7;
}
.footer__mail span {
  color: #ffffff;
}
.footer__tel {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 22px;
}
.footer__tel span {
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
}
.footer__tel-list {
  display: flex;
  flex-direction: column;
}
.footer__tel-list a {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #ffffff;
  transition: opacity 0.3s;
}
.footer__tel-list a:hover {
  opacity: 0.7;
}
.footer__main {
  padding-top: 40px;
  padding-bottom: 54px;
}
.footer__logo {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  gap: 15px;
}
.footer__logo img {
  flex-shrink: 0;
  width: 248px;
  height: 45px;
}
.footer__logo span {
  flex-shrink: 0;
  font-size: 13px;
  line-height: 21px;
  color: #ffffff;
}
@media only screen and (max-width: 1100px) {
  .footer__logo {
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 40px;
    justify-content: center;
  }
}
.footer__bottom {
  width: 100%;
  padding: 24px 0;
  border-top: 1px solid #ffffff;
  color: #9B9EA0;
  font-family: "Onest", sans-serif;
  font-optical-sizing: auto;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  font-style: normal;
}
.footer__bottom a {
  color: #9B9EA0;
  transition: opacity 0.3s;
  font-size: 14px;
  line-height: 21px;
}
.footer__bottom a:hover {
  opacity: 0.7;
}
.footer__bottom-content {
  display: flex;
  align-items: center;
  width: 55%;
}
@media only screen and (max-width: 1160px) {
  .footer__bottom-content {
    width: 100%;
    justify-content: space-between;
  }
}
@media only screen and (max-width: 585px) {
  .footer__bottom-content {
    width: 100%;
    flex-direction: column;
    gap: 5px;
  }
}
.footer__bottom-privacy {
  margin-right: 100px;
}
@media only screen and (max-width: 1160px) {
  .footer__bottom-privacy {
    margin: 0;
  }
}
.footer__bottom-studio {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.footer__bottom-studio img {
  margin-top: 2px;
  height: 11px;
  width: 56px;
}
.footer__bottom-studio span {
  padding-right: 14px;
}
@media only screen and (max-width: 1160px) {
  .footer__bottom-studio {
    margin: 0;
  }
}